@import url(https://fonts.googleapis.com/css?family=Libre+Baskerville:400,400i);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*Font CSS*/

    @font-face {
        font-family: GilroyExtraBold;
        src: url(/romanguillermo/static/media/Gilroy-ExtraBold.0f6e082f.otf) format("opentype");
    }

    @font-face {
        font-family: GilroyLight;
        src: url(/romanguillermo/static/media/Gilroy-Light.c62aded7.otf) format("opentype");
    }
/*End Font Css*/


.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.fullscreen {
  width: 100vw;
  height: 100vh;
}

.halfscreen {
  width: 30%;
  height: auto;
}

/* Images */
   .bgImg {
    background-position: center;
    background-size: cover;
  }

  .content1 {
    background-image: url(/romanguillermo/static/media/Content1.9f513ba1.jpg)
  }

  .aboutImg {
    height: auto;
    max-width: 100%;
  }
/* End Images */

/* Flex Properties */
.flexB50 {
  flex-basis: 50%;
}
/* End FLex */

/* About */ 
  #About {
    padding: 0 10rem;
  }

  .Container {
    display: grid;
    grid-template-columns: 1fr;
  }

  .containerFlex {
    display: flex;
    align-items: center;
  }

  .columnGrid {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 0.05fr 1fr;
  }

  .aboutInfo {
    font-size: 1.25rem;
    text-align: left;
    padding: 0 3rem;
  }

  .aboutContent {
    font-size: 1.25rem;
    letter-spacing: 0.0333333rem;
    line-height: 1.8rem;
  }

  .aboutTitle {
    font-size: 3rem;
  }
/* End About */

/* Contact */
  #Contact {
    padding-left: 5rem;
    padding-right: 5rem;
  }

  .contactGrid {
    display: grid;
    justify-content: left;
    grid-template-columns: 1fr;
  }
  .contactTitle {
    font-size: 2.25rem;
  }
  .contactParagraph {
    text-align: left;
    font-size: 1.5rem;
    font-family: 'GilroyLight';
  }
/* End Contact */

@media (max-width: 1100px) {
  .aboutTitle {
    font-size: 2rem;
  }
  #About {
    padding: 0 5rem;
  }

  .aboutContent {
    font-size: 1.05rem;
    letter-spacing: 0.0333333rem;
    line-height: 1.65rem;
  }
  .contactTitle {
    font-size: 1.9rem;
  }
  .contactParagraph {
    text-align: left;
    font-size: 1.3rem;
    font-family: 'GilroyLight';
  }
}

@media (max-width: 775px) {
    #About {
      padding: 0 2rem;
    }
  .aboutTitle {
    font-size: 1.7rem;
  }
  .aboutContent {
    font-size: 1rem;
    letter-spacing: 0.0333333rem;
    line-height: 1.5rem;
  }
  .contactTitle {
    font-size: 1.5rem;
  }
  .contactParagraph {
    text-align: left;
    font-size: 1rem;
    font-family: 'GilroyLight';
  }
}

#ReactGridGallery {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
}

.ReactGridGallery_tile {
  background: none !important;
}
